import React, { useState } from 'react';
import SectionTitle from '../../SectionTitle';
import icon from '../../../constants/icon';
import img from '../../../constants/img';
import {imgRotate} from '../../../utils/imgRotate';
import DetailPersonnel from './DetailPersonnel';

const introText='Vi nhng thnh phn thin nhin mang n nhng tc dng tt cho ln da, c bo ch, pha trn theo t l vng thch hp cng cng ngh hin i tin tin. V th hiu qu tr nm ca dng kem ny c nh gi kh tt. C th nh sau:Kem tr nm dng cho ban ngy vi thnh phn Glycerol momonnostearate c tc dng thm thu nhanh, su vo t bo da, ngn chn, c ch s hnh thnh melanin.Khng gian sang trng, t vn cng ok, dch v cng kh n nhng hi t. nh gi 3 sao v nhn vin mn t ni l check in g y nhng tht ra l t nh gi 5 sao...Xem thm'
export const utilsList = [
      { icon: icon.car, text: 'Bi  xe', count: '200m2' },
      { icon: icon.bed, text: 'Ging', count: '200' },
      { icon: icon.door, text: 'Phng', count: '20' },
      { icon: icon.car, text: 'Sc cha', count: '20 ngi' }
]
export const staffList=[
      {avatar:img.nv, name:'Nguyn Tun Anh', exp:2, position:'CV t vn'},
      {avatar:img.nv, name:'V Th Thanh Nhn', exp:2, position:'K thut vin'},
      {avatar:img.nv, name:'on Minh Nht', exp:5, position:'BS t vn'},
      {avatar:img.nv, name:'Trn Vn Khnh', exp:1, position:'K thut nng cao'},
      {avatar:img.nv, name:'Nguyn Hong Phc', exp:2, position:'K thut vin'},
]
function DetailInfo(props:any) {
      const {merDetail, t} = props;
      const [showViewMore, setShowViewMore] = useState(false)
      return (
            <div className="mer-detail__content-info">
                  <div className="mer__content-info__introduction">
                        <SectionTitle
                              title={merDetail?.name}
                        />
                        <div
                              style={showViewMore === true ? { height: 'fit-content', boxShadow: 'unset' } : {}}
                              className='text'
                        >
                              {introText}
                        </div>
                        <span
                              onClick={() => setShowViewMore(!showViewMore)}
                        >
                              <img
                                    style={showViewMore === true ? imgRotate : {}}
                                    src={icon.down} alt=""
                              />
                              {showViewMore === false ? t('Mer_de.view_more') : t('Mer_de.hide')}
                        </span>
                  </div>
                  <div className="mer__content-info__util">
                        <SectionTitle
                              title={t('Mer_de.utilities')}
                        />
                        <ul className="mer__content-info__util-list">
                              {
                                    utilsList.map((item, index) => (
                                          <li key={index}>
                                                <div className="flex-row-sp mer__content-info__util-item">
                                                      <img src={item.icon} alt="" />
                                                      <span>
                                                            <h5>{item.text}</h5>
                                                            <p>{item.count}</p>
                                                      </span>
                                                </div>
                                          </li>
                                    ))
                              }
                        </ul>
                  </div>
                  <div className="mer__content-info__personnel">
                        <SectionTitle
                              title={t('Mer_de.staff')}
                        />
                        <DetailPersonnel
                              t={t}
                              list={staffList}
                        />
                  </div>
            </div>
      );
}

export default DetailInfo;