import React, { useContext, useState } from 'react';
import SectionTitle from '../../SectionTitle';
import icon from '../../../constants/icon';
import {imgRotate} from '../../../utils/imgRotate'
import { AppContext } from '../../../context/AppProvider';

function DetailDesc(props:any) {
      const {product} = props;
      const [viewMore, setViewMore] = useState(false);
      const { t } = useContext(AppContext)
      return (
            <div className="product-desc">
                  <SectionTitle
                        title={product.product_name}
                  />
                  <div 
                        style={viewMore === true ? {height:'fit-content', boxShadow:'unset'}:{}}
                        className="product-desc__text"
                  >
                        Vi nhng thnh phn thin nhin mang n nhng tc
                        dng tt cho ln da, c bo ch, pha trn theo t
                        l vng thch hp cng cng ngh hin i tin tin.
                        V th hiu qu tr nm ca dng kem ny c nh gi
                        kh tt. C th nh sau: CNG DNG: Kem tr nm dng cho ban
                        ngy vi thnh phn Glycerol momonnostearate c tc
                        dng thm thu nhanh, su vo t bo da,
                        ngn chn, c ch s hnh thnh melanin.
                  </div>
                  <span
                        onClick={() => setViewMore(!viewMore)}
                        className="flex-row product-desc__more"
                  >
                        <img style={viewMore === true ? imgRotate : {}} src={icon.down} alt="" />
                        {viewMore === true ? t('Mer_de.hide') : t('Mer_de.view_more')}
                  </span>
            </div>
      );
}

export default DetailDesc;